import React, { useRef } from 'react';
import { Link } from 'react-router-dom'
import './header.scss';

const Header = () => {
    const mBtn = useRef();
    const mBtnIcon = useRef();
    const subNavIntro = useRef();
    const subNavExh = useRef();
    const subNavPer = useRef();
    const subNavData = useRef();
    const subNavWatch = useRef();
    const subNavInfo = useRef();
    const mBtnRemove = () => {
        mBtn.current.classList.toggle('on');
    }
    const subToggleIntro = () => {
        subNavIntro.current.classList.toggle('on');
        subNavExh.current.classList.remove('on');
        subNavPer.current.classList.remove('on');
        subNavData.current.classList.remove('on');
        subNavWatch.current.classList.remove('on');
        subNavInfo.current.classList.remove('on');
    }
    const subToggleExh = () => {
        subNavIntro.current.classList.remove('on');
        subNavExh.current.classList.toggle('on');
        subNavPer.current.classList.remove('on');
        subNavData.current.classList.remove('on');
        subNavWatch.current.classList.remove('on');
        subNavInfo.current.classList.remove('on');
    }
    const subTogglePer = () => {
        subNavIntro.current.classList.remove('on');
        subNavExh.current.classList.remove('on');
        subNavPer.current.classList.toggle('on');
        subNavData.current.classList.remove('on');
        subNavWatch.current.classList.remove('on');
        subNavInfo.current.classList.remove('on');
    }
    const subToggleData = () => {
        subNavIntro.current.classList.remove('on');
        subNavExh.current.classList.remove('on');
        subNavPer.current.classList.remove('on');
        subNavData.current.classList.toggle('on');
        subNavWatch.current.classList.remove('on');
        subNavInfo.current.classList.remove('on');
    }
    const subToggleWatch = () => {
        subNavIntro.current.classList.remove('on');
        subNavExh.current.classList.remove('on');
        subNavPer.current.classList.remove('on');
        subNavData.current.classList.remove('on');
        subNavWatch.current.classList.toggle('on');
        subNavInfo.current.classList.remove('on');
    }
    const subToggleInfo = () => {
        subNavIntro.current.classList.remove('on');
        subNavExh.current.classList.remove('on');
        subNavPer.current.classList.remove('on');
        subNavData.current.classList.remove('on');
        subNavWatch.current.classList.remove('on');
        subNavInfo.current.classList.toggle('on');
    }


    return (
        <header id="header-wrap" className="inner">
            <h1 id="logo"><Link to="/"><img src="img/logo_kor.gif" alt="로고이미지" /></Link></h1>
            <div id="m-nav-div" ref={mBtn}>
                <div id="m-nav-icon" ref={mBtnIcon} onClick={mBtnRemove}>
                    <span></span><span></span><span></span>
                </div>
                <div id="m-nav">
                    <div id="lang">
                        <ul>
                            <li><Link to="/">English</Link></li>
                            <li><Link to="/">Chinese</Link></li>
                            <li><Link to="/">Japanese</Link></li>
                        </ul>
                    </div>
                    <ul id="main-nav">
                        <li className="nav-tit" ref={subNavIntro} onClick={subToggleIntro}>미술관 소개
                            <ul id="sub-nav">
                                <li><Link to="/">미술관소개</Link></li>
                                <li><Link to="/">시설안내</Link></li>
                                <li><Link to="/">오시는길</Link></li>
                                <li><Link to="/">주변안내</Link></li>
                            </ul>
                        </li>
                        <li className="nav-tit" ref={subNavExh} onClick={subToggleExh}>전시
                            <ul id="sub-nav">
                                <li><Link to="/exhibit">현재전시</Link></li>
                                <li><Link to="/">지난전시</Link></li>
                                <li><Link to="/">예정전시</Link></li>
                            </ul>
                        </li>
                        <li className="nav-tit" ref={subNavPer} onClick={subTogglePer}>교육/행사
                            <ul id="sub-nav">
                                <li><Link to="/">어린이</Link></li>
                                <li><Link to="/">청소년</Link></li>
                                <li><Link to="/">일반인</Link></li>
                                <li><Link to="/">전시연계</Link></li>
                                <li><Link to="/">학술</Link></li>
                            </ul>
                        </li>
                        <li className="nav-tit" ref={subNavData} onClick={subToggleData}>소장자료
                            <ul id="sub-nav">
                                <li><Link to="/">저작권 안내</Link></li>
                                <li><Link to="/">소장작품</Link></li>
                                <li><Link to="/">도서자료</Link></li>
                            </ul>
                        </li>
                        <li className="nav-tit" ref={subNavWatch} onClick={subToggleWatch}>관람/예약
                            <ul id="sub-nav">
                                <li><Link to="/">관람안내</Link></li>
                                <li><Link to="/">사전예약</Link></li>
                                <li><Link to="/">단체예약</Link></li>
                            </ul>
                        </li>
                        <li className="nav-tit" ref={subNavInfo} onClick={subToggleInfo}>정보마당
                            <ul id="sub-nav">
                                <li><Link to="/">공지사항</Link></li>
                                <li><Link to="/">자료실</Link></li>
                                <li><Link to="/">자원봉사</Link></li>
                            </ul>
                        </li>
                    </ul>
                </div>
            </div>
            <nav>
                <div id="lang">
                    <p className="lang-tit">Language
                        <ul>
                            <li><Link to="/">English</Link></li>
                            <li><Link to="/">Chinese</Link></li>
                            <li><Link to="/">Japanese</Link></li>
                        </ul>
                    </p>
                </div>
                <ul id="main-nav">
                    <li className="nav-tit">미술관 소개
                        <ul id="sub-nav">
                            <li><Link to="/">미술관소개</Link></li>
                            <li><Link to="/">시설안내</Link></li>
                            <li><Link to="/">오시는길</Link></li>
                            <li><Link to="/">주변안내</Link></li>
                        </ul>
                    </li>
                    <li className="nav-tit">전시
                        <ul id="sub-nav">
                            <li><Link to="/exhibit">현재전시</Link></li>
                            <li><Link to="/">지난전시</Link></li>
                            <li><Link to="/">예정전시</Link></li>
                        </ul>
                    </li>
                    <li className="nav-tit">교육/행사
                        <ul id="sub-nav">
                            <li><Link to="/">어린이</Link></li>
                            <li><Link to="/">청소년</Link></li>
                            <li><Link to="/">일반인</Link></li>
                            <li><Link to="/">전시연계</Link></li>
                            <li><Link to="/">학술</Link></li>
                        </ul>
                    </li>
                    <li className="nav-tit">소장자료
                        <ul id="sub-nav">
                            <li><Link to="/">저작권 안내</Link></li>
                            <li><Link to="/">소장작품</Link></li>
                            <li><Link to="/">도서자료</Link></li>
                        </ul>
                    </li>
                    <li className="nav-tit">관람/예약
                        <ul id="sub-nav">
                            <li><Link to="/">관람안내</Link></li>
                            <li><Link to="/">사전예약</Link></li>
                            <li><Link to="/">단체예약</Link></li>
                        </ul>
                    </li>
                    <li className="nav-tit">정보마당
                        <ul id="sub-nav">
                            <li><Link to="/">공지사항</Link></li>
                            <li><Link to="/">자료실</Link></li>
                            <li><Link to="/">자원봉사</Link></li>
                        </ul>
                    </li>
                </ul>
            </nav>
        </header>
    )
}

export default Header